<template>
	<v-app id="app">
		<div id="repartos" class="myTable">
			<!--HEAD DE LA PAGINA -->
			<vue-headful
				title="repartos - Aranda Distribucion"
			></vue-headful>

			<h2>Listado de repartos</h2>

			<b-button
				@click="testFetch"
				class="mb-4"
				title="Recargar"
				variant="light"
			>
				<v-icon dark style="color: black">mdi-cached</v-icon>
				Actualizar
			</b-button>
			<!-- ================ALTA repartoS======================== -->
			<b-button
				class="mb-4 ml-2"
				v-b-modal.modal-alta
				@click="altareparto()"
				title="Nuevo reparto"
				style="color: white"
			>
				<v-icon dark> mdi-plus </v-icon>
				Nuevo reparto
			</b-button>
			<b-modal no-close-on-backdrop id="modal-alta" hide-footer>
				<template #modal-title><h5 class="modal-title">Alta</h5></template>
				<reparto-alta :updateTable="testFetch" />
			</b-modal>
			<!-- =========================================================== -->

			<!-- ==================================CREAR PDF================================== -->
			<b-button
				@click="generarPDF()"
				id="btn_down_pdf"
				class="mb-4 ml-2"
				title="Generar PDF"
				variant="danger"
				style="color: white"
				:disabled="btn_down_pdf"
			>
				<svg
					xmlns="http://www.w3.org/2000/svg"
					width="16"
					height="16"
					fill="currentColor"
					class="bi bi-file-pdf-fill"
					viewBox="0 0 16 16"
				>
					<path
						d="M5.523 10.424c.14-.082.293-.162.459-.238a7.878 7.878 0 0 1-.45.606c-.28.337-.498.516-.635.572a.266.266 0 0 1-.035.012.282.282 0 0 1-.026-.044c-.056-.11-.054-.216.04-.36.106-.165.319-.354.647-.548zm2.455-1.647c-.119.025-.237.05-.356.078a21.035 21.035 0 0 0 .5-1.05 11.96 11.96 0 0 0 .51.858c-.217.032-.436.07-.654.114zm2.525.939a3.888 3.888 0 0 1-.435-.41c.228.005.434.022.612.054.317.057.466.147.518.209a.095.095 0 0 1 .026.064.436.436 0 0 1-.06.2.307.307 0 0 1-.094.124.107.107 0 0 1-.069.015c-.09-.003-.258-.066-.498-.256zM8.278 4.97c-.04.244-.108.524-.2.829a4.86 4.86 0 0 1-.089-.346c-.076-.353-.087-.63-.046-.822.038-.177.11-.248.196-.283a.517.517 0 0 1 .145-.04c.013.03.028.092.032.198.005.122-.007.277-.038.465z"
					/>
					<path
						fill-rule="evenodd"
						d="M4 0h8a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2zm.165 11.668c.09.18.23.343.438.419.207.075.412.04.58-.03.318-.13.635-.436.926-.786.333-.401.683-.927 1.021-1.51a11.64 11.64 0 0 1 1.997-.406c.3.383.61.713.91.95.28.22.603.403.934.417a.856.856 0 0 0 .51-.138c.155-.101.27-.247.354-.416.09-.181.145-.37.138-.563a.844.844 0 0 0-.2-.518c-.226-.27-.596-.4-.96-.465a5.76 5.76 0 0 0-1.335-.05 10.954 10.954 0 0 1-.98-1.686c.25-.66.437-1.284.52-1.794.036-.218.055-.426.048-.614a1.238 1.238 0 0 0-.127-.538.7.7 0 0 0-.477-.365c-.202-.043-.41 0-.601.077-.377.15-.576.47-.651.823-.073.34-.04.736.046 1.136.088.406.238.848.43 1.295a19.707 19.707 0 0 1-1.062 2.227 7.662 7.662 0 0 0-1.482.645c-.37.22-.699.48-.897.787-.21.326-.275.714-.08 1.103z"
					/>
				</svg>
				Generar PDF
			</b-button>
			<!-- ============================================================================== -->

			<b-button
				class="mb-4 ml-2"
				variant="danger"
				id="btn_del_full"
				title="Eliminar todos los registros"
				style="color: white"
				:disabled="btn_del_full"
				v-b-modal.modal-eliminarTodo
			>
				<v-icon class="mr-2" style="color: white"> mdi-delete </v-icon>
				Eliminar
			</b-button>

			<!-- ================ELIMINAR VARIOS repartos======================== -->
			<div>
				<b-modal
					no-close-on-backdrop
					ref="my-modal"
					id="modal-eliminarTodo"
					hide-footer
					title="Eliminar"
					ok-only
				>
					<div class="d-bk text-center" v-if="selected.length === rows">
						<h3>¿Esta seguro de eliminar todos los registros ?</h3>
					</div>
					<div class="d-bk text-center" v-else>
						<h3>¿Esta seguro de eliminar {{ selected.length }} registros ?</h3>
					</div>

					<b-button class="mt-2" bk @click="hideModal" title="Volver Atras">
						Volver Atras
					</b-button>

					<b-button
						class="mt-3"
						variant="danger"
						bk
						title="Eliminar"
						@click="delete_all_repartos()"
					>
						Eliminar
					</b-button>
				</b-modal>
			</div>

			<!-- ======== Formulario de Busqueda ======== -->
			<b-form-group
				label-for="filter-input"
				label-align-sm="right"
				label-size="sm"
				class="mb-0"
				style="width: 100%; padding-bottom: 1em"
				v-show="rows > 0"
			>
				<b-input-group size="sm">
					<b-form-input
						id="filter-input"
						v-model="filter"
						type="search"
						placeholder="Buscar registros"
					></b-form-input>

					<b-input-group-append>
						<b-button :disabled="!filter" @click="filter = ''"
							>Limpiar</b-button
						>
					</b-input-group-append>
				</b-input-group>
			</b-form-group>

			<!-- ======================================== -->

			<div v-if="rows > 0">
				<div v-if="selected.length > 0">
					<div v-if="rows != rowsFilter">
						<pre>
Registros Fitrados: {{ rowsFilter }} | Filas seleccionadas: {{
								selected.length
							}}</pre>
					</div>
					<div v-else>
						<pre>
Cantidad de registros: {{ rows }} | Filas seleccionadas: {{
								selected.length
							}}</pre>
					</div>
				</div>
				<div v-else>
					<div v-if="rows != rowsFilter">
						<pre>Registros Fitrados: {{ rowsFilter }} </pre>
					</div>
					<div v-else>
						<pre>Cantidad de registros: {{ rows }}</pre>
					</div>
				</div>
				<b-button class="mb-4 ml-2"
						  size="sm"
						  style="color: white"
						  title="Seleccionar Todo"
						  @click="seleccionar_todas"
						  :disabled="btn_select">
					Seleccionar Todo
				</b-button>
				<b-button class="mb-4 ml-2"
						  size="sm"
						  style="color: white"
						  title="Limpiar Seleccion"
						  @click="limpiar_seleccion"
						  :disabled="btn_limpiar">
					Limpiar Seleccion
				</b-button>
				<b-button class="mb-4 ml-2"
						  size="sm"
						  style="color: white"
						  variant="success"
						  title="Activar Todo"
						  @click="activarTodos()"
						  :disabled="btn_select">
					Activar Todos
				</b-button>
				<b-button class="mb-4 ml-2"
						  size="sm"
						  style="color: white"
						  variant="danger"
						  title="Desactivar Todo"
						  @click="desactivarTodos()"
						  :disabled="btn_select">
					Desactivar Todos
				</b-button>
			</div>
			<div v-else>
				<pre>Cantidad de registros: {{ rows }}</pre>
			</div>

			<section class="container">
				<b-table :fields="fields"
						 striped
						 sortable
						 responsive
						 hover
						 :items="
						tabla_med

					"
						 show-empty
						 :sticky-header="true"
						 :no-border-collapse="false"
						 ref="tablaregistros"
						 id="tablaregistros"
						 selectable
						 select-mode="multi"
						 :filter="filter"
						 @filtered="onFiltered"
						 @row-selected="seleccionar_una"
						 :per-page="perPage"
						 :current-page="currentPage">
					<template #empty="">
						<!--
		<b>No hay registros para mostrar</b>
		-->
						<b>{{ msj_tabla }}</b>
					</template>

					<template #cell(selected)="{ rowSelected }">
						<template v-if="rowSelected">
							<span aria-hidden="true">&check;</span>
							<span class="sr-only">Selected</span>
						</template>
						<template v-else>
							<span aria-hidden="true">&nbsp;</span>
							<span class="sr-only">Not selected</span>
						</template>
					</template>

					<template slot="cell(id_camion)" slot-scope="data">
						{{ data.value.split("/")[4] }}
					</template>
					<template slot="cell(cuentaactiva)" slot-scope="data">
						<div v-if="data.value === true">
							<svg xmlns="http://www.w3.org/2000/svg"
								 width="16"
								 height="16"
								 fill="currentColor"
								 class="bi bi-check-circle-fill"
								 viewBox="0 0 16 16"
								 style="color: green">
								<path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z" />
							</svg>
							SI
						</div>
						<div v-else>
							<svg xmlns="http://www.w3.org/2000/svg"
								 width="16"
								 height="16"
								 fill="currentColor"
								 class="bi bi-x-circle-fill"
								 viewBox="0 0 16 16"
								 style="color: red">
								<path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z" />
							</svg>
							NO
						</div>
					</template>
					<template slot="cell(action)" slot-scope="row">
						<div class="mt-3">
							<b-button-group>
								<b-button variant="info"
										  id="button-1"
										  title="Mostrar Info"
										  :disabled="btn_mostrar"
										  @click="row.toggleDetails">
									{{ row.detailsShowing ? "Ocultar" : "Mostrar" }} Detalles
								</b-button>

								<b-button variant="warning"
										  id="button-2"
										  title="Editar este registro"
										  v-b-modal.modal-editar
										  @click="editarreparto(row.item, row.index)"
										  :disabled="btn_editar">
									<v-icon class="mr-2"> mdi-pencil </v-icon>
									Editar
								</b-button>

								<b-button :to="{
										name: 'ClientesRep',
										params: { repa: row.item },
										query: { id: row.item.id_reparto },
									}"
										  variant="primary"
										  id="button-3"
										  title="Ver clientes asociados"
										  :disabled="btn_clientes"
										  style="color: white">
									<v-icon dark>mdi-format-list-bulleted-square</v-icon>
									Clientes
								</b-button>

								<b-button variant="success"
										  id="button-2"
										  title="Cerrar este registro"
										  v-b-modal.modal_cerrar
										  @click="actdesactRepa(row.item)"
										  :disabled="btn_cerrar">
									<v-icon class="mr-2"> mdi-check-bold </v-icon>
									Act/Des
								</b-button>

								<b-button variant="danger"
										  id="button-3"
										  @click="showModalinfo(row.item, row.index)"
										  title="Eliminar este registro"
										  :disabled="btn_eliminar">
									<v-icon class="mr-2"> mdi-delete </v-icon>
									Eliminar
								</b-button>
							</b-button-group>
						</div>
					</template>
					<template #row-details="row">
						<b-card>
							<b-list-group horizontal>
								<b-list-group>
									<b-list-group-item>
										<b>Nombre :</b>
										{{ row.item.nombre.toUpperCase() }}
									</b-list-group-item>
									<b-list-group-item>
										<b>Camion:</b>
										{{ row.item.camion }}
									</b-list-group-item>
									<b-list-group-item>
										<b>Usuario:</b>
										{{ row.item.usuario }}
									</b-list-group-item>
									<b-list-group-item>
										<b>Contraseña:</b>
										{{ row.item.contrasenia }}
									</b-list-group-item>
									<b-list-group-item>
										<b>Clientes:</b>
										<div v-for="cliente in row.item.clientes_reparto"
											 :key="cliente">
											<ul>
												<li>
													<b>ID cliente: {{ cliente.split("/")[4] }}</b>
													<!-- {{ cliente.cliente }} -->
												</li>
											</ul>
										</div>
									</b-list-group-item>
								</b-list-group>
								&nbsp;
							</b-list-group>
						</b-card>
					</template>
				</b-table>
				<b-container fluid>
					<b-col class="my-1">
						<b-pagination
							v-model="currentPage"
							align="center"
							pills
							:total-rows="totalRows"
							:per-page="perPage"
							aria-controls="tabla_med"
						>
						</b-pagination>
					</b-col>
				</b-container>
			</section>
			<aside v-show="rows > 0">
				<div>
					<b-card-group deck>
						<b-card
							bg-variant="primary"
							text-variant="white"
							header="REGISTROS POR PAGINA"
							class="text-center"
						>
							<b-form-group label-for="per-page-select" class="mb-0">
								<b-form-select
									id="per-page-select"
									v-model="perPage"
									:options="pageOptions"
									size="sm"
								></b-form-select>
							</b-form-group>
						</b-card>
					</b-card-group>
				</div>

				<br />

				<div>
					<b-card-group deck>
						<b-card
							bg-variant="primary"
							text-variant="white"
							header="FILTRAR POR"
							class="text-center"
						>
							<div class="accordion" role="tablist">
								<b-card no-body>
									<b-card-header header-tag="header" class="p-1" role="tab">
										<b-button
											bk
											v-b-toggle.accordion-1
											variant="info"
											style="font-size: 0.82em"
										>
											PROVEEDOR
										</b-button>
									</b-card-header>
									<b-collapse
										id="accordion-1"
										visible
										accordion="my-accordion"
										role="tabpanel"
									>
										<b-card-body>
											<b-form-group id="input-group-4">
												<v-autocomplete
													id="letra"
													v-model="filter_proveedor"
													:items="options_proveedor"
													type="text"
													solo
													filled
												></v-autocomplete>
												<div v-show="filter_proveedor != null">
													<b-button
														@click="filter_proveedor = null"
														title="Limpiar"
													>
														Limpiar
													</b-button>
												</div>
											</b-form-group>
										</b-card-body>
									</b-collapse>
								</b-card>
							</div>
						</b-card>
					</b-card-group>
				</div>
			</aside>
			<!-- ================EDITAR UN reparto======================== -->
			<b-modal no-close-on-backdrop id="modal-editar" hide-footer>
				<template #modal-title><h5 class="modal-title">Editar</h5></template>
				<reparto-update :item_med="editar" :updateTable="testFetch" />
			</b-modal>

			<!-- ================ELIMINAR UN reparto======================== -->
			<b-modal
				no-close-on-backdrop
				id="modal_eliminar"
				ref="my-modal"
				hide-footer
				title="Eliminar"
				ok-only
			>
				<div class="d-bk text-center">
					<h3>
						¿Esta seguro de eliminar los datos de '{{
							infoEliminar.reparto.nombre
						}}'?
					</h3>
				</div>
				<b-button class="mt-2" bk @click="hideModal" title="Volver Atras">
					Volver Atras
				</b-button>

				<b-button
					class="mt-3"
					variant="danger"
					bk
					title="Eliminar"
					@click="deletereparto(infoEliminar.reparto.id_reparto)"
				>
					Eliminar
				</b-button>
			</b-modal>

			<!-- ==================================CREAR PDF================================== -->
			<vue-html2pdf
				:show-layout="false"
				:float-layout="true"
				:enable-download="false"
				:preview-modal="true"
				:paginate-elements-by-height="1400"
				filename="Listado de repartos"
				:pdf-quality="2"
				:manual-pagination="false"
				pdf-format="a4"
				pdf-orientation="portrait"
				pdf-content-width="80%"
				@progress="onProgress($event)"
				@startPagination="startPagination()"
				@hasPaginated="hasPaginated()"
				@beforeDownload="beforeDownload($event)"
				@hasDownloaded="hasDownloaded($event)"
				ref="html2Pdf"
			>
				<section slot="pdf-content">
					<!-- PDF Content Here -->
					<section class="pdf-item">
						<h3>Aranda Distribuciones repartos</h3>
						<img
							src="@/assets/logo.jpg"
							alt="Logo Federación"
							srcset=""
							id="Logo_fed"
						/>
					</section>
					<section class="pdf-item">
						<h3>Listado de repartos</h3>
						<b-table
							:fields="fieldsPDF"
							responsive
							:items="tabla_med"
							:no-border-collapse="false"
							small
							fixed
							bordered
							head-variant="light"
						>
							<template slot="cell(id_camion)" slot-scope="data">
								{{ data.value.split("/")[4] }}
							</template>
						</b-table>
					</section>
				</section>
			</vue-html2pdf>
			<!-- ============================================================================== -->
		</div>
	</v-app>
</template>

<script>
    let api = new URL("http://alhost:8081");
	api.pathname = "repartos";
	//api.port = 8000;
	//api.port = 8081;

	import repartoAlta from "./RepartosAlta.vue";
	import repartoUpdate from "./RepartosUpdate.vue";
	import axios from "axios";
	import VueHtml2pdf from "vue-html2pdf";
    import { APIControler } from "@/store/APIControler";

	export default {
		components: { repartoAlta, repartoUpdate, VueHtml2pdf },
		data() {
			return {
				tabla_med: [],
                clientes: [],
				fields: [
					{ key: "selected", label: "Seleccionar", sortable: true },
					{ key: "id_reparto", label: "ID", sortable: true },
					{ key: "nombre", label: "Nombre", sortable: true },
					{ key: "camion", label: "Camion", sortable: true },
                    { key: "cuentaactiva", label: "Activo", sortable: true },
                    { key: "action", label: "Acciones", variant: "secondary" },
				],
				fieldsPDF: [
                    { key: "selected", label: "Seleccionar", sortable: true },
                    { key: "id_reparto", label: "ID", sortable: true },
                    { key: "nombre", label: "Nombre", sortable: true },
                    { key: "camion", label: "Id de Camion", sortable: true },
				],
				editar: {},
				//buscar: '',
				filter: null,
				selected: [],
				infoEliminar: {
					id: "modal_eliminar",
					reparto: -1,
				},

				//Botones
				btn_down_pdf: true, //Desabilito los botones, hasta que muestre los datos
				btn_del_full: true,
				msj_tabla: " Presione 'Mostrar' para ver los regitros ",
				btn_mostrar: false,
                btn_clientes: false,
				btn_editar: false,
				btn_eliminar: false,
				btn_select: false,
				btn_limpiar: true,

				totalRows: 1, //Total de filas
				currentPage: 1, //Pagina actual
				perPage: 10, // Datos en la tabla por pagina
				pageOptions: [10, 20, 40, 100, { value: 10000, text: "Todos" }],

				//Opciones de filtro
				options_proveedor: [
					{ value: null, text: "Elija un Proveedor", selected: true },
				],

				//Campos a filtrar:
				filter_proveedor: null,
			};
		},
		computed: {
			rows() {
				return (this.totalRows = this.tabla_med.length);
			},
			rowsFilter() {
				return this.totalRows;
			},
			sortOptions() {
				// Create an options list from our fields
				return this.fields
					.filter((f) => f.sortable)
					.map((f) => {
						return { text: f.label, value: f.key };
					});
			},
		},
		methods: {
			//Funcion para mostrar todos los repartos
			async testFetch() {
				try {
					const res = await fetch(api);
					const data = await res.json();

					var lista_med = data.results;

					console.log(lista_med);

					this.tabla_med = lista_med;

					this.tabla_med.forEach((element) => {
						let opcionFarm = {};
						opcionFarm.value = element.letra;
                        opcionFarm.text = element.letra.split("/")[4];
						if (
							this.options_proveedor.find((x) => x.value == opcionFarm.value)
						) {
							console.log(opcionFarm, " ya se encuentra en el listado");
						} else {
							this.options_proveedor.push(opcionFarm);
						}
					});

					this.btn_down_pdf = false; //Habilito los botones

					if (this.tabla_med.length == 0) {
						this.msj_tabla = " No se encuentran regitros en esta tabla ";
					}
				} catch (error) {
					console.log(error);
				}
			},
			//Funcion para mostrar el modal
			showModal() {
				this.$refs["my-modal"].show();
			},
			//Funcion para esconder el modal
			hideModal() {
				this.$refs["my-modal"].hide();
			},
            redirect() {
                if (!this.$session.exists()) {
                    window.ation.replace('/login');
                }
                if (this.$session.get('tipocuenta') == 'reparto') {
                    window.ation.replace('/Home');
                }

            },
			info(id) {
				alert("id: " + id);
			},

			showModalinfo(item, index) {
				this.infoEliminar.reparto = item;
				this.showModal();
			},

			//-----Funciones de seleccion
			//Selecciona una a una
			seleccionar_una(items) {
				this.selected = items;
				if (this.selected.length > 0) {
					this.btn_del_full = false;
					this.btn_limpiar = false;
					if (this.selected.length > 1) {
						this.btn_mostrar = true;
						this.btn_editar = true;
						this.btn_eliminar = true;
					}
					if (this.selected.length == this.rows) {
						this.btn_select = true;
					} else {
						this.btn_select = false;
					}
				} else {
					this.btn_del_full = true;
					this.btn_mostrar = false;
					this.btn_editar = false;
					this.btn_eliminar = false;
					this.btn_limpiar = true;
				}
			},
			//Selecciona todas
			seleccionar_todas() {
				this.$refs.tablaregistros.selectAllRows();
				this.btn_del_full = false;
				this.btn_mostrar = true;
				this.btn_editar = true;
				this.btn_eliminar = true;

				this.btn_select = true;
				this.btn_limpiar = false;
			},
			//Limpia todas las selecciones
			limpiar_seleccion() {
				this.$refs.tablaregistros.clearSelected();
				this.btn_del_full = true;
				this.btn_mostrar = false;
				this.btn_editar = false;
				this.btn_eliminar = false;

				this.btn_select = false;
				this.btn_limpiar = true;
			},

			//Funcion para crear el PDF
			generarPDF() {
				if (this.tabla_med.length != 0) {
					this.$refs.html2Pdf.generatePdf();
				} else {
					swal("Debe tener al menos 1 registro");
				}
			},

			altareparto() {},
			editarreparto(item, index) {
				this.editar = item;
			},
			async deletereparto(id) {
				axios
                    .delete("http://alhost:8081/repartos/" + id + "/")
					.then((datos) => {
						swal("Eliminacion Exitosa", " ", "success");
						console.log(datos);
						this.hideModal();
					})
					.catch((error) => {
						swal("¡ERROR!", "Se ha detectado un problema ", "error");
						console.log(error);
						this.hideModal();
					})
					.finally(() => this.testFetch());
			},

			//Funcion para eliminar todos los repartos seleccionados
			async delete_all_repartos() {
				var cantidad = this.selected.length;

				try {
					for (var i = 0; i < cantidad; i++) {
						axios.delete(
							"http://alhost:8081/repartos/" +
								this.selected[i].id_reparto +
								"/"
						);
						if (this.selected.length == 0) {
							console.log("Eliminacion Exitosa");
							break;
						}
					}
					this.hideModal();
					swal("Eliminacion Exitosa", " ", "success");
				} catch (error) {
					this.hideModal();
					swal("¡ERROR!", "Se ha detectado un problema ", "error");
					console.log(error);
				} finally {
					this.testFetch();
				}
			},

			onFiltered(filteredItems) {
				// Trigger pagination to update the number of buttons/pages due to filtering
				this.totalRows = filteredItems.length;
				this.currentPage = 1;
			},
            async getClientes() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "clientes/";
                this.clientes = await productosAPI.getData();
			},
            getCliente(id) {
                if (this.clientes == []) {
                    return getClientes().then(() => {
                        return this.getCliente(id)
                    })
                }
                else {
                    return this.clientes.find(s => {
                        return s.numero_cliente == id
                    })
                }
			},
			activarTodos()
			{
				this.tabla_med.forEach((repa) =>
				{
                    this.activarRepa(repa);
				})
			},
            desactivarTodos() {
                this.tabla_med.forEach((repa) => {
                    this.desactivarRepa(repa);
                })
            },
            async actdesactRepa(item) {
                var baseURL = "http://alhost:8081"
                const response = await axios.get(baseURL + "/repartos/" + item.id_reparto + "/actdesactReparto/")

                    .then((datos) => {
                        swal("Operación Exitosa", " ", "success");
                    })
                    .catch((error) => {
                        swal("¡ERROR!", "Se ha detectado un problema ", "error");
                        console.log(error);
                    })
                    .finally(() => this.testFetch());
			},
            async activarRepa(item) {
                var baseURL = "http://alhost:8081"
                const response = await axios.get(baseURL + "/repartos/" + item.id_reparto + "/activarReparto/")

                    .then((datos) => {
                        swal("Operación Exitosa", " ", "success");
                    })
                    .catch((error) => {
                        swal("¡ERROR!", "Se ha detectado un problema ", "error");
                        console.log(error);
                    })
                    .finally(() => this.testFetch());
			},
            async desactivarRepa(item) {
                var baseURL = "http://alhost:8081"
                const response = await axios.get(baseURL + "/repartos/" + item.id_reparto + "/desactivarReparto/")

                    .then((datos) => {
                        swal("Operación Exitosa", " ", "success");
                    })
                    .catch((error) => {
                        swal("¡ERROR!", "Se ha detectado un problema ", "error");
                        console.log(error);
                    })
                    .finally(() => this.testFetch());
            },
			//Edicion del pdf antes de descargar
			async beforeDownload({ html2pdf, options, pdfContent }) {
				await html2pdf()
					.set(options)
					.from(pdfContent)
					.toPdf()
					.get("pdf")
					.then((pdf) => {
						const totalPages = pdf.internal.getNumberOfPages();
						for (let i = 1; i <= totalPages; i++) {
							pdf.setPage(i);
							pdf.setFontSize(10);
							pdf.setTextColor(150);
							pdf.text(
								"Página " + i + " of " + totalPages,
								pdf.internal.pageSize.getWidth() * 0.88,
								pdf.internal.pageSize.getHeight() - 0.3
							);
						}
					})
					.save();
			},
		},

		beforeMount() {
			this.testFetch();
			this.redirect();
			this.getClientes();
		},
	};
</script>

