<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Datos</h4>
		<b-form @submit.stop.prevent>
			<!--
	<b-form-group
		label="*ID"
		label-for="id_medicamento"
		@submit.stop.prevent="handleSubmit"
	>
		<b-form-input
			id="id_medicamento"
			v-model="medicamentos.id_medicamento"
			type="number"
			:state="validacion.id_medicamento.estado"
			placeholder="Ingrese un Numero"
			invalid-feedback="Complete este campo"
			required
		>
		</b-form-input>
		<b-form-invalid-feedback id="id_medicamento-live-feedback">
			{{ validacion.id_reparto.mensaje }}
		</b-form-invalid-feedback>
	</b-form-group>
	-->

			<b-form-group label="*Nombre"
						  label-for="nombre"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="nombre"
							  v-model="repartos.nombre"
							  type="text"
							  placeholder="Ingrese el nombre"
							  invalid-feedback="Complete este campo"
							  :state="validacion.nombre.estado"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="nombre-live-feedback">
					{{ validacion.nombre.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Usuario"
						  label-for="usuario"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="usuario"
							  v-model="repartos.usuario"
							  type="text"
							  placeholder="Ingrese el usuario"
							  invalid-feedback="Complete este campo"
							  :state="validacion.usuario.estado"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="usuario-live-feedback">
					{{ validacion.usuario.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Contrase�a"
						  label-for="contrasenia"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="contrasenia"
							  v-model="repartos.contrasenia"
							  type="password"
							  placeholder="Ingrese la contrasenia"
							  invalid-feedback="Complete este campo"
							  :state="validacion.contrasenia.estado"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="contrasenia-live-feedback">
					{{ validacion.contrasenia.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="Camion"
						  label-for="camion"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="camion"
							  v-model="repartos.camion"
							  type="text"
							  placeholder="Ingrese un Camion"
							  :state="validacion.camion.estado">
				</b-form-input>
				<b-form-invalid-feedback id="camion-live-feedback">
					{{ validacion.camion.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Clientes"
						  label-for="cliente"
						  ref="lista"
						  id="listaclis"
						  v-slot="{ ariaDescribedby }">
				<input type="text" id="buscadorepico1" v-on:keyup="buscarnow()" class="buscador1" ref="buscadormenu1" placeholder="Buscar Clientes..." />
				<b-form-checkbox-group v-model="repartos.clientes_reparto"
									   :options="op_clientes"
									   :aria-describedby="ariaDescribedby"
									   name="flavour-2a"
									   stacked></b-form-checkbox-group>
			</b-form-group>

		</b-form>
		<b-button class="mt-2" variant="success" bk @click="postreparto()">Guardar</b-button>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				repartos: {},
				respuesta: {},
                listarep: [],
				camiones: {},
				data: {},
                op_clientes: [],
				options: [{ value: null, text: "Elija un camion", disabled: true }],

				text: "",
				validacion: {
					id_reparto: { estado: null, mensaje: "" },
					nombre: { estado: null, mensaje: "" },
					camion: { estado: null, mensaje: "" },
                    usuario: { estado: null, mensaje: "" },
                    contrasenia: { estado: null, mensaje: "" },
				},
			};
		},

		methods: {
			checkearCliente(id_cliente) {
				let id = 0;
				let retorno = 0
				this.listarep.forEach((element) => {
					element.clientes_reparto.forEach((cliente) => {
						id = parseInt(cliente.split('/')[4]);
						if (id == id_cliente)
						{
							retorno = 1;
						}
					});
				});
				return retorno;
			},
            async getClientes() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "clientes/";
				this.data = await productosAPI.getData();
                let camionAPI = new APIControler();
                camionAPI.apiUrl.pathname = "repartos/";
                this.listarep = await camionAPI.getData(this.camiones);
                let option
                this.data.forEach((element) => {
                    option = {};
                    option.value =
						"http://alhost:8081/clientes/" + element.numero_cliente + "/";
					console.log(this.checkearCliente(element.numero_cliente));
					if (this.checkearCliente(element.numero_cliente) == 1) {
						option.text = element.nombre + ' - Ya usado en otro reparto';
					} else
					{
                        option.text = element.nombre;
					}
                    console.log(option);
                    this.op_clientes.push(option);
                });
			},

			/*
    async getMedicamento() {
      let medicamentosAPI = new APIControler();
      this.data = await medicamentosAPI.getData();
    },*/

			async postreparto() {
				let repartosAPI = new APIControler();
                repartosAPI.apiUrl.pathname = "repartos/";
				//this.data = await medicamentosAPI.postData(this.medicamentos);
                this.respuesta = await repartosAPI.postData(this.repartos);
				this.cargarFeedback();
				this.resetForm();

				this.updateTable();
			},
            async buscarnow() {
                // Declare variables
                var input, filter, ul, li, a, i, txtValue;
                input = this.$refs.buscadormenu1;
                filter = input.value.toUpperCase();
                ul = document.getElementById('listaclis');
				li = ul.getElementsByTagName('div');

                // Loop through all list items, and hide those who don't match the search query
				for (i = 2; i < li.length; i++) {
                    console.log(li[i]);
					a = li[i].getElementsByTagName('span');
					console.log(a)
					txtValue = a[0].outerText;
                    console.log(txtValue)
                    if (txtValue.toUpperCase().indexOf(filter) > -1) {
                        li[i].style.display = "";
                    } else {
                        li[i].style.display = "none";
                    }
                }
            },
			async resetForm() {
                this.repartos.id_reparto = null;
                this.repartos.nombre = "";
                this.repartos.camion = "";
			},

			cargarFeedback() {
				let valido;
				if (!this.respuesta) this.respuesta = {};
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		},
		beforeMount() {
			this.getClientes();
		},
		/*
  computed: {
      validation() {
        return this.text.length > 0 ? true : false;
      },
    }
  */
	};
</script>

<style>
    #buscadorepico1{
        background-color:white;
        width: 100%;
		height:2em;
		margin-bottom:1em;
        font-size: 16px;
        border: 1px solid grey;
		border-radius:1em;
        padding-left: 1em;
		color:black;
    }
    .buscador1::placeholder {
		color:grey;
    }
</style>
