<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Datos</h4>
		<b-form @submit.stop.prevent>
			<b-form-group label="*ID"
						  label-for="id_reparto"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="id_reparto"
							  v-model="item_med.id_reparto"
							  type="number"
							  :state="validacion.id_reparto.estado"
							  placeholder="Ingrese un Numero"
							  invalid-feedback="Complete este campo"
							  disabled
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="id_reparto-live-feedback">
					{{ validacion.id_reparto.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Nombre"
						  label-for="nombre"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="nombre"
							  v-model="item_med.nombre"
							  type="text"
							  placeholder="Ingrese el nombre"
							  invalid-feedback="Complete este campo"
							  :state="validacion.nombre.estado"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="nombre-live-feedback">
					{{ validacion.nombre.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Camion"
						  label-for="camion"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="camion"
							  v-model="item_med.camion"
							  type="text"
							  placeholder="Ingrese un Camion"
							  invalid-feedback="Complete este campo"
							  required
							  :state="validacion.camion.estado">
				</b-form-input>
				<b-form-invalid-feedback id="camion-live-feedback">
					{{ validacion.camion.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Clientes"
						  label-for="cliente"
						  ref="lista"
						  id="listaclis"
						  v-slot="{ ariaDescribedby }">
				<input type="text" id="buscadorepico1" v-on:keyup="buscarnow()" class="buscador1" ref="buscadormenu1" placeholder="Buscar Clientes..." />
				<b-form-checkbox-group v-model="item_med.clientes_reparto"
									   :options="op_clientes"
									   :aria-describedby="ariaDescribedby"
									   name="flavour-2a"
									   stacked></b-form-checkbox-group>
			</b-form-group>
		</b-form>

		<b-button class="mt-2" variant="success" bk @click="putreparto()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";
	import axios from "axios";
	1;
	export default {
		props: {
			item_med: {},
			updateTable: Function,
		},
		data() {
			return {
				// medicamentos: {},
				camiones: {},
				data: {},
                op_clientes: [],
				respuesta: {},
				listarep: [],
				options: [{ value: null, text: "Elija un camion", disabled: true }],
				validacion: {
					id_reparto: { estado: null, mensaje: "" },
					nombre: { estado: null, mensaje: "" },
                    camion: { estado: null, mensaje: "" },
				},
			};
		},
		created: function () {
			//console.log('Funcion realizada');
			this.getCamiones();
		},
		methods: {
            async getCamiones() {
                let camionAPI = new APIControler();
                camionAPI.apiUrl.pathname = "camiones/";
                this.data = await camionAPI.getData(this.camiones);
                this.data.forEach((element) => {
                    let option = {};
                    option.value =
                        "https://arandadistribucion.pythonanywhere.com/camiones/" + element.id_camion + "/";
                    option.text = element.id_camion + " - " + element.patente;
                    console.log(option);
                    this.options.push(option);
                });
			},
            async buscarnow() {
                // Declare variables
                var input, filter, ul, li, a, i, txtValue;
                input = this.$refs.buscadormenu1;
                filter = input.value.toUpperCase();
                ul = document.getElementById('listaclis');
                li = ul.getElementsByTagName('div');

                // Loop through all list items, and hide those who don't match the search query
                for (i = 2; i < li.length; i++) {
                    console.log(li[i]);
                    a = li[i].getElementsByTagName('span');
                    console.log(a)
                    txtValue = a[0].outerText;
                    console.log(txtValue)
                    if (txtValue.toUpperCase().indexOf(filter) > -1) {
                        li[i].style.display = "";
                    } else {
                        li[i].style.display = "none";
                    }
                }
            },
            async getClientes() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "clientes/";
                this.data = await productosAPI.getData();
                let camionAPI = new APIControler();
                camionAPI.apiUrl.pathname = "repartos/";
                this.listarep = await camionAPI.getData();
                let option
                this.data.forEach((element) => {
                    option = {};
                    option.value =
                        "http://alhost:8081/clientes/" + element.numero_cliente + "/";
                    console.log(this.checkearCliente(element.numero_cliente));
                    if (this.checkearCliente(element.numero_cliente) != null) {
                        option.text = element.nombre + ' - Ya usado en reparto nro ' + this.checkearCliente(element.numero_cliente);
                    } else {
                        option.text = element.nombre;
                    }
                    console.log(option);
                    this.op_clientes.push(option);
                });
			},
            checkearCliente(id_cliente) {
                let id = 0;
                let retorno = null
                this.listarep.forEach((element) => {
                    element.clientes_reparto.forEach((cliente) => {
                        id = parseInt(cliente.split('/')[4]);
                        if (id == id_cliente) {
                            retorno = element.id_reparto;
                        }
                    });
                });
                return retorno;
            },
			async putreparto() {
				let resp = "vacio";
				await axios
					.put(
						"http://alhost:8081/repartos/" +
							this.item_med.id_reparto +
							"/",
						this.item_med
					)
					.then(function () {
						swal("Operación Exitosa", " ", "success");
					})
					.catch(function (error) {
						const mje =
							error.response.status < 500
								? "Los datos no son válidos"
								: "Se ha detectado un problema ";
						swal("¡ERROR!", mje, "error");
						resp = error.response.data;
					});
				this.respuesta = resp;
				this.cargarFeedback();
				this.updateTable();
			},
			cargarFeedback() {
				let valido;
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		},
        beforeMount() {
            this.getClientes();
        },
	};
</script>

<style></style>
